import React from 'react';
import gh from '../images/svgIcons/github.svg';
import fcc from "../images/svgIcons/freecodecamp.svg";
import as from "../images/svgIcons/artstation.svg";

const Footer = ()=> {
    return (
        <footer>
            <ul>
                <li className="first"><a href="https://www.freecodecamp.org/"><img height="30" width="30" id="fcc" src={fcc} alt="link to free code camp"/></a></li>
                <li><a href="https://github.com/aerinkayne"><img height="24" width="24" id="gh" src={gh} alt="link to github account" /></a></li>
                <li><a href="https://www.artstation.com/aerin-kayne"><img id="as" height="24" width="24" src={as} alt="link to artstation account" /></a></li>
			    <li className="last"><a href="mailto:darth_kayne@yahoo.com">Contact</a></li>    
            </ul>
        </footer> 
    )
}

export default Footer;