import React from 'react';


const Navbar = ()=> {

    function handleClick(event){
        let t = event.currentTarget;
        let divs = Array.from(t.children);
        let elNavList = document.getElementById('navlist');
        divs.forEach(div => div.classList.toggle('clicked'));
        elNavList.classList.toggle('hide');

    }
    return (
        <nav>
            <div>
                <div id="logo"><span>&#x270E;</span></div>
                
                <ul id="navlist">
                    <li><a href='https://aerinkayne.github.io/gh_portfolio/#/'>main site</a></li>
                </ul>
                
                <div id="burger" onClick={handleClick}>
                    <div id="topB" className="clicked"></div>
                    <div id="midB" className="clicked"></div>
                    <div id="btmB" className="clicked"></div>
                </div>
            </div>    
        </nav>
    )
}

export default Navbar;